import Vue from "vue";
import Router from "vue-router";
import home from "@/components/home";
import produtos from "@/components/produtos";
import sistemas from "@/components/sistemas";
import sobre from "@/components/sobre";

Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "",
      component: home,
    },
    {
      path: "/home",
      name: "home",
      component: home,
    },
    {
      path: "/produtos",
      name: "produtos",
      component: produtos,
    },
    {
      path: "/sistemas",
      name: "sistemas",
      component: sistemas,
    },
    {
      path: "/sobreaxpointer",
      name: "sobre",
      component: sobre,
    },
  ],
});
